
"use client";
import { useEffect, useState } from 'react';

export default function ContactsTable() {
  const [contacts, setContacts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch('/api/contacts')
      .then(res => res.json())
      .then(data => {
        setContacts(data);
        setLoading(false);
      });
  }, []);

  if (loading) return <div>Loading contacts...</div>;

  return (
    <table className="data-table">
      <thead>
        <tr>
          <th>Name</th>
          <th>Email</th>
          <th>Phone</th>
          <th>Account</th>
        </tr>
      </thead>
      <tbody>
        {contacts.map((c: any) => (
          <tr key={c.id}>
            <td>{c.name}</td>
            <td>{c.email}</td>
            <td>{c.phone}</td>
            <td>{c.accountId}</td>
          </tr>
        ))}
      </tbody>
    </table>
  );
}
